import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import forumList from '@/views/pages/forum/list'
import xueshengList from '@/views/pages/xuesheng/list'
import xueshengDetail from '@/views/pages/xuesheng/formModel'
import xueshengAdd from '@/views/pages/xuesheng/formAdd'
import xueshengRegister from '@/views/pages/xuesheng/register'
import xueshengCenter from '@/views/pages/xuesheng/center'
import weixiuyuanList from '@/views/pages/weixiuyuan/list'
import weixiuyuanDetail from '@/views/pages/weixiuyuan/formModel'
import weixiuyuanAdd from '@/views/pages/weixiuyuan/formAdd'
import sushexinxiList from '@/views/pages/sushexinxi/list'
import sushexinxiDetail from '@/views/pages/sushexinxi/formModel'
import sushexinxiAdd from '@/views/pages/sushexinxi/formAdd'
import baoxiuxinxiList from '@/views/pages/baoxiuxinxi/list'
import baoxiuxinxiDetail from '@/views/pages/baoxiuxinxi/formModel'
import baoxiuxinxiAdd from '@/views/pages/baoxiuxinxi/formAdd'
import baoxiuchuliList from '@/views/pages/baoxiuchuli/list'
import baoxiuchuliDetail from '@/views/pages/baoxiuchuli/formModel'
import baoxiuchuliAdd from '@/views/pages/baoxiuchuli/formAdd'
import jieguofankuiList from '@/views/pages/jieguofankui/list'
import jieguofankuiDetail from '@/views/pages/jieguofankui/formModel'
import jieguofankuiAdd from '@/views/pages/jieguofankui/formAdd'
import weixiubaoxiuList from '@/views/pages/weixiubaoxiu/list'
import weixiubaoxiuDetail from '@/views/pages/weixiubaoxiu/formModel'
import weixiubaoxiuAdd from '@/views/pages/weixiubaoxiu/formAdd'
import baoxiufenpeiList from '@/views/pages/baoxiufenpei/list'
import baoxiufenpeiDetail from '@/views/pages/baoxiufenpei/formModel'
import baoxiufenpeiAdd from '@/views/pages/baoxiufenpei/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'forumList',
			component: forumList
		}
		, {
			path: 'xueshengList',
			component: xueshengList
		}, {
			path: 'xueshengDetail',
			component: xueshengDetail
		}, {
			path: 'xueshengAdd',
			component: xueshengAdd
		}
		, {
			path: 'xueshengCenter',
			component: xueshengCenter
		}
		, {
			path: 'weixiuyuanList',
			component: weixiuyuanList
		}, {
			path: 'weixiuyuanDetail',
			component: weixiuyuanDetail
		}, {
			path: 'weixiuyuanAdd',
			component: weixiuyuanAdd
		}
		, {
			path: 'sushexinxiList',
			component: sushexinxiList
		}, {
			path: 'sushexinxiDetail',
			component: sushexinxiDetail
		}, {
			path: 'sushexinxiAdd',
			component: sushexinxiAdd
		}
		, {
			path: 'baoxiuxinxiList',
			component: baoxiuxinxiList
		}, {
			path: 'baoxiuxinxiDetail',
			component: baoxiuxinxiDetail
		}, {
			path: 'baoxiuxinxiAdd',
			component: baoxiuxinxiAdd
		}
		, {
			path: 'baoxiuchuliList',
			component: baoxiuchuliList
		}, {
			path: 'baoxiuchuliDetail',
			component: baoxiuchuliDetail
		}, {
			path: 'baoxiuchuliAdd',
			component: baoxiuchuliAdd
		}
		, {
			path: 'jieguofankuiList',
			component: jieguofankuiList
		}, {
			path: 'jieguofankuiDetail',
			component: jieguofankuiDetail
		}, {
			path: 'jieguofankuiAdd',
			component: jieguofankuiAdd
		}
		, {
			path: 'weixiubaoxiuList',
			component: weixiubaoxiuList
		}, {
			path: 'weixiubaoxiuDetail',
			component: weixiubaoxiuDetail
		}, {
			path: 'weixiubaoxiuAdd',
			component: weixiubaoxiuAdd
		}
		, {
			path: 'baoxiufenpeiList',
			component: baoxiufenpeiList
		}, {
			path: 'baoxiufenpeiDetail',
			component: baoxiufenpeiDetail
		}, {
			path: 'baoxiufenpeiAdd',
			component: baoxiufenpeiAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/xueshengRegister',
		component: xueshengRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
